.about-view-container{
  // height: 100vh;

  .about-line-one, .about-line-two, .about-line-three{
    white-space: nowrap;
    overflow: hidden;
    word-spacing: -10px;
    background-color:none;
    span{
      background-color: none;
      font-size: 0.6rem;
    }
  }
  .about-line-one{
    height: 50px;
    span{
      line-height: 50px;
    }
    h1{
      transform: translateX(-150px);
    }
  }
  .about-line-two{
    line-height: 50px;
    span{
      height: 50px;
    }
    :not(span.outline){
      color: rgba(255,255,255,0.5)
    }
    .outline{
      -webkit-text-stroke: 2px rgba(255,255,255,0.5);
    }
    h1{
      transform: translateX(-150px);
    }
  }
