.title-view-container{
  min-height: 100vh;
  margin-top: 30%;
  margin-bottom: 20%;
  .line-container{
    margin-bottom: 20%;
    .title-line-one, .title-line-two{
      white-space: nowrap;
      overflow: hidden;
      word-spacing: -10px;
      span{
        background-color: none;
        font-size: 1rem;
      }
      h1{
        background-color: none;
      }
    }
    .title-line-one{
      // margin-top: 400px;
      height: 86px;
      span{
        line-height: 86px;
      }
    }
    .title-line-two{
      line-height: 86px;
      span{
        height: 86px;
      }
      h1{
        transform: translateX(-80%);
      }
    }
  }

  h3{
    margin-top: 50px;
    text-align: center;
  }
  .icons-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: 600px;
    margin: 50px auto 100px auto;

    .icon-box{
      padding: 5px;
      display: flex;
      flex-direction: column;
      img{
        height: 120px;
      }
      p{
        text-align: center;
      }
    }
  }
  // h2{
  //   text-align: center;
  //   color: yellow;
